	methods: {
		//根据index进行页面跳转
		goPage(i){
			switch (i){
				case 0:
					break;
				case 1:
					break;
				case 2:
					break;
				case 3:
					uni.navigateTo({
						url:'../becomeJJR/becomeJJR'
